.tile         {position: absolute; left: 0; top: 0; width: 60px; height: 60px; -webkit-transition: left .3s ease-in-out, top .3s ease-in-out; transition: left .3s ease-in-out, top .3s ease-in-out; display: flex; justify-content: center; align-items: center; box-sizing: border-box;}
.tile:before  {content: ""; display: block; padding-top: 100%;}

.tile             {background-color: #e4b95a; border: 1px solid #fe6c53; cursor: pointer; z-index: 2;}
.tile.tile--empty {background-color: #fff; border: 0; z-index: 1;}
.tile__inner      {font-size: 1.5rem;}

.tiles {position: relative; width: calc(60px * 4); height: calc(60px * 4);}

.app                        {text-align: center;}

.container                  {width: calc(60px * 4);  margin: 0 auto;}
.container                  {border: 1px solid #7f5c3e; padding: 3px;}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

